body {
  color: #bfffd1;
  background-color: #000;
  font-family: Arial, sans-serif;
}

h1 {
  font-size: 42px;
  font-stretch: condensed;
}

.website-container {
  grid-template-columns: 2fr 3fr;
  height: 100vh;
  display: grid;
  overflow: hidden;
}

.logo {
  width: 20%;
}

.left-homepage {
  height: 100vh;
  padding: 24px;
  overflow-y: auto;
}

.right-homepage {
  height: 100vh;
  overflow-y: auto;
}

.right-homepage img {
  width: 100%;
}

@media screen and (width <= 768px) {
  .website-container {
    grid-template-columns: 1fr;
    height: auto;
    display: grid;
    overflow: visible;
  }

  .left-homepage {
    height: 100%;
  }

  .logo {
    width: 40%;
  }

  h1 {
    font-size: 32px;
  }
}

@media screen and (width >= 769px) and (width <= 1268px) {
  .website-container {
    grid-template-columns: 2fr 1fr;
    height: auto;
    display: grid;
    overflow: visible;
  }

  .logo {
    width: 40%;
  }

  h1 {
    font-size: 32px;
  }
}
